<template>
  <BaseLayout>
    <Breadcrumbs />
    <div v-if="category.brand == null" class="category">
      <CategoryOverview
        v-if="category.is_anchor == 0"
        :category="category"
        :categories="categories"
      />
      <div class="container" v-if="category.is_anchor == 1">
        <div class="row">
          <ProductFilter class="col-lg-3" />
          <ProductsListingV2 class="col-lg-9" />
        </div>
        <div v-if="categoryForm != null">
          <dynamic-forms :category_id="category.id" type="category" />
        </div>
      </div>
    </div>
    <div v-if="category.brand != null">
      <div class="container clearfix">
        <div class="category_description" v-html="category.description"></div>
        <div class="brand_logo">
          <img :src="category.brand.logo" />
        </div>
      </div>
      <CategoryOverview :category="category" />
      <div class="container" v-if="category.is_anchor == 1">
        <div class="row">
          <ProductFilter class="col-lg-3" />
          <ProductsListingV2 class="col-lg-9" />
        </div>
        <div v-if="categoryForm != null">
          <dynamic-forms :category_id="category.id" type="category" :categories="categories" />
        </div>
      </div>
    </div>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted, onUnmounted, watch } from "vue";
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";
import { useGtm } from "@gtm-support/vue-gtm";
import { useHead } from "@unhead/vue";
import config from "@config";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";
import ProductFilter from "@/base/core/components/ProductFilter";
import CategoryOverview from "@/base/core/components/CategoryOverview";
import ProductsListingV2 from "@/base/core/components/ProductListingV2";
import DynamicForms from "@/base/core/components/DynamicForms";

const route = useRoute();
const router = useRouter();
const store = useStore();
const gtm = useGtm();

const category = computed(() => store.getters["category/getCurrentCategory"]);
const categories = computed(() => category.value.children);
const categoryForm = computed(() => store.getters["forms/getCategoryForm"]);
const gtmProducts = computed(() => store.getters["category/getGtmProducts"]);

useHead({
  meta: [
    { charset: "utf-8" },
    { name: "viewport", content: "width=device-width, initial-scale=1" },
    {
      name: "title",
      content: () => (category.value ? category.value.meta_title ?? category.value.name ?? "" : ""),
    },
    {
      name: "keywords",
      content: () => (category.value ? category.value.meta_keywords ?? "" : ""),
    },
    {
      name: "description",
      content: () => (category.value ? category.value.meta_description ?? "" : ""),
    },
  ],
  title: () => category.value.meta_title ?? category.value.name ?? "",
});

const setBreadcrumb = () => {
  const breadcrumbs = category.value.breadcrumbs;
  let currentName = "undefined";
  if (typeof category.value.name == "string") {
    currentName = category.value.name;
  }
  const bcrumb = { current: currentName, routes: [] };
  if (breadcrumbs != null) {
    breadcrumbs.sort((a, b) => {
      if (a.category_level < b.category_level) return -1;
      if (a.category_level > b.category_level) return 1;
      return 0;
    });
    let path = "";
    breadcrumbs.forEach((element) => {
      if (path.length > 0) {
        path = path + "/";
      }
      path = path + element.category_url_key;
      let name = "undefined";
      if (typeof element.category_name == "string") {
        name = element.category_name;
      }
      const bc = {
        name: name,
        route_link: "/" + path,
      };
      bcrumb.routes.push(bc);
    });
  }
  store.commit("breadcrumbs/set", bcrumb);
};

watch(gtmProducts, () => {
  if (gtm.enabled()) {
    if (gtmProducts.value.length > 0) {
      window.dataLayer.push({
        event: "eec.impressionView",
        ecommerce: {
          impressions: gtmProducts.value,
        },
      });
    }
  }
});

onUnmounted(() => store.commit("category/setLoadProducts", false));

onMounted(() => setBreadcrumb());

watch(
  () => route.path,
  () => {
    const query = {};
    const pageSize = config.products?.perPage;
    const perPage = store.getters["category/getProductsPerPage"];
    if (perPage != pageSize) query["page-size"] = perPage;
    router.replace({ path: route.path, query: query });
  }
);
</script>
