<template>
  <section class="best-sellers">
    <div class="best-seller--category">{{ category.name }}</div>
    <span v-if="bestsellers.length > 0" class="section-title">{{ $t("bestsellers_for_you") }}</span>
    <div v-if="bestsellers.length > 0" class="row">
      <div
        class="col-6 col-md-3 position-relative best-seller--card-holder"
        v-for="product of bestsellers.slice(0, 4)"
        :key="product.sku"
      >
        <b-link
          v-if="$store.getters['user/isProductInWishlist'](product.sku) && isLoggedIn"
          class="add-to-wishlist"
          @click="onClickWishListIcon(product.sku, product.__typename, product.url_key)"
        >
          <div class="heart" v-if="$store.getters['user/isProductInWishlist'](product.sku)">
            <img src="@/base/assets/heart.png" alt="heart" />
          </div>
          <i class="lnr lnr-heart" v-else />
        </b-link>
        <b-link class="bestsellers-card" :to="`/` + product.url_key">
          <div
            class="bestsellers-card--img"
            :style="{ backgroundImage: `url(${product.image.medium})` }"
          >
            <div class="actionbuttons">
              <span class="sale-txt" v-if="product.sale == 1">Sale</span>
              <span class="new-txt" v-if="product.new == 1">Nieuw</span>
            </div>
          </div>
          <span class="bestsellers-card--product-name">{{ product.name }}</span>

          <span
            v-if="
              product.price_range.minimum_price.final_price.value ==
              product.price_range.maximum_price.final_price.value
            "
            class="d-block bestsellers-card--price"
            >&euro;{{
              product.price_range.minimum_price.final_price.value.toFixed(2).replace(".", ",")
            }}</span
          >
          <span
            v-if="
              product.price_range.minimum_price.final_price.value !=
              product.price_range.maximum_price.final_price.value
            "
            class="d-block bestsellers-card--price"
            >&euro;{{
              product.price_range.minimum_price.final_price.value.toFixed(2).replace(".", ",")
            }}
            - &euro;{{
              product.price_range.maximum_price.final_price.value.toFixed(2).replace(".", ",")
            }}</span
          >

          <span
            class="d-block bestsellers-card--sale-comment"
            v-if="product.price_range.minimum_price.discount.percent_off > 0"
            >{{ $t("discount") }}
            {{ product.price_range.minimum_price.discount.percent_off.toFixed(0) }}%</span
          >
          <span
            class="d-block bestsellers-card--sale-comment"
            style="visibility: hidden"
            v-if="product.price_range.minimum_price.discount.percent_off == 0"
            >{{ $t("discount") }}
            {{ product.price_range.minimum_price.discount.percent_off.toFixed(0) }}%</span
          >
        </b-link>
      </div>
    </div>
  </section>
</template>

<script setup>
import { computed, onMounted, watch } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import VueCookies from "vue-cookies";
import { useI18n } from "vue-i18n";
import { isServer } from "@storefront/core/helpers";
import config from "@config";

const store = useStore();
const { t } = useI18n();
const router = useRouter();

const bestsellers = computed(() => {
  let bestsellers = [];
  if (config.helloRetail && config.helloRetail.enabled) {
    bestsellers = store.getters["category_local/getBestsellerProducts"];
  } else {
    bestsellers = store.getters["category/getBestSellersSimple"];
  }
  return bestsellers;
});

const category = computed(() => store.getters["category/getCurrentCategory"]);
const isLoggedIn = computed(() => store.getters["user/getIsLoggedIn"]);

async function loadBestsellers() {
  if (!isServer) {
    if (config.helloRetail && config.helloRetail.enabled) {
      const hello_retail_id = VueCookies.get("hello_retail_id");
      await store.dispatch("category_local/loadBestsellerProducts", {
        category: category.value.name,
        hello_retail_id,
      });
    } else {
      await store.dispatch("category/loadBestsellerProducts");
    }
  }
}

function onClickWishListIcon(productId, typename, url_key) {
  if (store.getters["user/isProductInWishlist"](productId) == true) {
    store.dispatch("user/setProductWishlistStatus", {
      sku: productId,
      parentSku: null,
      store: store,
    });
  } else {
    if (typename == "SimpleProduct") {
      store.dispatch("user/setProductWishlistStatus", {
        sku: productId,
        parentSku: null,
        store: store,
      });
    } else {
      const msg = {
        type: "danger",
        title: t("wishlist error"),
        text: t("wishlist_select_options"),
      };
      store.dispatch("messages/sendMessage", { message: msg });
      router.push("/" + url_key);
    }
  }
}

watch(() => category.value.name, loadBestsellers);
onMounted(loadBestsellers);
</script>
