<template>
  <div class="content-filter">
    <div class="cst-pagination-bar">
      <label
        >Pagina<span>{{ currentPage }}</span
        >van<span>{{ Math.ceil(totalProds / prodsPerPage) }}</span>
      </label>
      <BPagination
        :total-rows="totalProds"
        :per-page="prodsPerPage"
        :model-value="currentPage"
        @update:model-value="changePage"
        class="cst-pagination"
        aria-controls="cat-item"
      >
        <template #page="{ page }">
          <BLink class="page-link" :to="goToPage(page)">
            {{ page }}
          </BLink>
        </template>
      </BPagination>
    </div>
    <div class="pagination-bar-down">
      <div class="left">
        <div class="display-drop">
          <label>{{ $t("view") }}:</label>
          <select :value="prodsPerPage" @change="changePerPage">
            <option value="12">12</option>
            <option value="24">24</option>
            <option value="36">36</option>
          </select>
        </div>
      </div>
      <div class="select-wrapper">
        <BFormSelect
          id="sorting"
          :options="selectOptions"
          class="select"
          @update:model-value="changeSorting"
          :model-value="sorting"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";
import { useI18n } from "vue-i18n";
import config from "@config";

const store = useStore();
const route = useRoute();
const router = useRouter();
const { t } = useI18n();

const sorting = computed(() => store.getters["category/getSorting"]);
const currentPage = computed(() => store.getters["category/getCurrentPage"]);
const totalProds = computed(() => store.getters["category/getTotalProducts"]);
const prodsPerPage = computed(
  () => store.getters["category/getProductsPerPage"]
);
const totalPages = computed(() => store.getters["category/getTotalPages"]);
const category = computed(() => store.getters["category/getCurrentCategory"]);

const selectOptions = computed(() => {
  const sortOpt = [];
  Object.keys(config.sortingOptions).forEach((element) => {
    const option = {
      value: element,
      text: t(`sortingOptions.${element}`),
    };
    sortOpt.push(option);
  });
  return sortOpt;
});

const changePage = (pageNo) => {
  router.currentRoute.value.meta = "top";
  store.commit("category/setCurrentPage", { pageNo, store });

  const { path } = route;
  const query = { ...route.query };

  if (pageNo != 1) query["page"] = pageNo;
  else delete query["page"];

  router.replace({ path, query });
};

const changePerPage = (event) => {
  router.currentRoute.value.meta = "top";
  const perPage = +event.target.value;
  const { path } = route;
  const query = { ...route.query };

  const pageSize = config.products?.perPage;

  if (perPage != pageSize) query["page-size"] = perPage;
  else delete query["page-size"];
  delete query["page"];

  router.replace({ path, query });

  store.commit("category/setProductsPerPage", { perPage, store });
};

const changeSorting = (sortVal) => {
  router.currentRoute.value.meta = "top";
  store.commit("category/setSorting", { sortVal, store });

  const { path } = route;
  const query = { ...route.query };

  const { sortDefault } = config;
  if (config.sortingOptions[sortVal] && sortVal !== sortDefault) {
    query["sort"] = sortVal;
  } else {
    delete query["sort"];
  }
  delete query["page"];

  router.replace({ path, query });
};

const goToPage = (page) => {
  const { path } = route;
  const query = { ...route.query };

  if (page != 1) query["page"] = page;
  else delete query["page"];

  return { path, query };
};

onMounted(() => {
  const { page } = route.query;
  const path = "/" + category.value.url_path;
  const query = { ...route.query };
  if (page && totalPages.value < parseInt(page)) {
    delete query["page"];
  }
  const pageSize = config.products?.perPage;
  const perPage = store.getters["category/getProductsPerPage"];
  if (perPage != pageSize) query["page-size"] = perPage;

  router.replace({ path, query });
});
</script>

<style lang="scss">
.page-item {
  button {
    height: 100%;
  }
}
</style>
